
.detail{position:relative;min-height:100vh;z-index:55
}
.shopnav{line-height:1.2rem;display:-ms-flexbox;display:flex;background-color:#fff
}
.shopnav-tab{position:relative;-ms-flex:1;flex:1;display:block;width:0;text-align:center;font-size:.373333rem;color:#666;text-decoration:none;border-bottom:1px solid #ddd
}
.shopnav-tab.active{color:#3190e8
}
.shopnav-tab.active .shopnav-title{position:relative
}
.shopnav-tab.active .shopnav-title:after{content:"";position:absolute;bottom:-.16rem;left:-.066667rem;right:-.066667rem;height:.053333rem;background-color:#3190e8;border-radius:.04rem
}
.menuview{color:#333;height:100%;box-sizing:border-box;-webkit-tap-highlight-color:transparent;-webkit-tap-highlight-color:transparent;font:.186667rem/1.2 Helvetica Neue,Microsoft Yahei,sans-serif
}
.menuview-main{height:16.586667rem;padding-bottom:1.28rem;background-color:#fff
}



